@mixin font-smoothing {
	-webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@mixin font-smoothing-none {
	-webkit-font-smoothing: auto;
  -moz-osx-font-smoothing: auto;
}


/* custom animations  */


/* fade in up */
@include keyFrame(customFadeInUp) {
	0% {
    	opacity: 0;
    	@include transform(translateY(20px))
  	}

  	100% {
    	opacity: 1;
    	@include transform(none);
  	}
}

.customFadeInUp {
	-webkit-animation-name: customFadeInUp;
			animation-name: customFadeInUp;
	animation-fill-mode: both;
	-webkit-animation-fill-mode: both;

    @include animationDuration(1s);
}

/* fade in down from .3 to 1 */
@include keyFrame(customFadeInDown) {
	0% {
    	opacity: 0;
    	@include transform(translateY(-25px))
  	}

  	100% {
    	opacity: 1;
    	@include transform(none);
  	}
}

.customFadeInDown {
	-webkit-animation-name: customFadeInDown;
            animation-name: customFadeInDown;
	animation-fill-mode: both;
	-webkit-animation-fill-mode: both;

    @include animationDuration(1s);
}

/* fade in down from 0 to 1 */

@include keyFrame(featureFadeInDown) {
	0% {
    	opacity: 0;
    	@include transform(translateY(-30px))
  	}

  	100% {
    	opacity: 1;
    	@include transform(none);
  	}
}

.featureFadeInDown {
	-webkit-animation-name: featureFadeInDown;
            animation-name: featureFadeInDown;
	animation-fill-mode: both;
	-webkit-animation-fill-mode: both;

    @include animationDuration(1s);
}

/* fade in & scale*/

@include keyFrame(fadeInScale) {
	0% {
		opacity: 0;
    	@include transform(scale3d(.89, .89, .89));
  	}

  	100% {
  		opacity: 1;
    	@include transform(none);
  	}
}

.fadeInScale {
	-webkit-animation-name: fadeInScale;
            animation-name: fadeInScale;
	animation-fill-mode: both;
	-webkit-animation-fill-mode: both;

    @include animationDuration(1s);
}

@include keyFrame(stripey) {
  0% {
      opacity: 0;
      @include transform(translate3d(0, 40%, 0) scale3d(0.92, 0.92, 0.92));
    }

    100% {
      opacity: 1;
      @include transform(none);
    }
}

.stripey {
  -webkit-animation-name: stripey;
            animation-name: stripey;
  animation-fill-mode: both;
  -webkit-animation-fill-mode: both;

    @include animationDuration(.7s);
}


/* global notification */

.global-notification {
	position: fixed;
	bottom: 40px;
	left: 40px;
	z-index: 99999;

	height: 65px;
	width: 65px;
	border-radius: 50px;
	background: #25BAF0;
	padding: 15px 30px;
	opacity: 0;

	/*cubic-bezier(0.25, 0.25, 0.4, 1.6)*/

	.message {
		opacity: 0;
		color: #fff;
		font-family: $helvetica;
		font-weight: 600;
		position: relative;
		top: -3px;
	}
}

@include keyFrame(notification) {
	0% {
		width: 65px;
		opacity: 1;
	}

	100% {
    width: 360px;
    opacity: 1;
	}
}

@include keyFrame(notification-remove) {
	0% {
		width: 360px;
		opacity: 1;
	}

	100% {
    width: 65px;
    opacity: 0;
    visibility: hidden;
	}
}

@include keyFrame(notification-child) {
	0% {
		opacity: 0;
	}

	100% {
	    opacity: 1;
	}
}

.uber-notification-remove {
	-webkit-animation-name: notification-remove;
            animation-name: notification-remove;

	-webkit-animation-fill-mode: forwards;
			animation-fill-mode: forwards;

	@include animationDuration(.7s);
	-webkit-animation-timing-function: cubic-bezier(.37,1.39,.41,.91);
}

.uber-notification {
  	-webkit-animation-name: notification;
            animation-name: notification;

	-webkit-animation-fill-mode: forwards;
			animation-fill-mode: forwards;

	@include animationDuration(.8s);

	@include animationDelay(.7s);
	-webkit-animation-timing-function: cubic-bezier(.37,1.39,.41,.91);

	.message {
		-webkit-animation-name: notification-child;
            animation-name: notification-child;

		-webkit-animation-fill-mode: both;
				animation-fill-mode: both;

		@include animationDuration(.55s);
		@include animationDelay(1s);
	}
}



