/* header pull-down change-section menu */
#header-sections-menu {
  position: relative;
  height: 150px;
  width: 100%;
  z-index: 9999;
  background: #0D283C;
  margin-top: -150px;

  @include transition(all .3s);

  &.fixed {
    position: fixed;
  }

  .links {
    position: relative;
    top: 35px;
    text-align: center;

    a {
      display: inline-block;
      margin: 0 50px;
      font-size: 14px;
      text-decoration: none;
      font-family: $helvetica;
      font-weight: 500;
      color: #fff;
      padding: 16px 20px;
      border-radius: 5px;

      @include transition(all .2s);

      @media(max-width: 767px) {
        font-size: 12px;
        padding: 15px 0;
        margin: 0;
        width: 31%;
      }

      &.active,
      &:hover {
        background: #16374F;
      }

      i {
        display: block;
        font-size: 25px;
        position: relative;
        top: -2px;

        @media(max-width: 767px) {
          font-size: 22px;
        }
      }
    }
  }
}
